.container {
  display: flex;
  flex-direction: row;
  padding-bottom: 36px;
  padding-left: 24px;
  font-family: 'Raleway';
}

.ava {
  position: relative;
  width: 200px;
  height: 200px;
}

.ava>label {
  cursor: pointer;
  border-radius: 4px;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 200px;
  width: 200px;
  background: rgba(0,0,0,0) url('data:image/png;base64,') center center no-repeat ;
  opacity: 0;
  transition: .3s;
}

.ava>label:hover {
  background: rgba(0,0,0,0.8) url('data:image/png;base64,') center center no-repeat ;
  opacity: 1;
  transition: .3s;
}

.ava>label>input { 
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  opacity: 0;
}

.ava>img {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  width: 200px;
  height: 200px;
  display: block;
}

.description {
  width: 100%;
  margin-left: 40px;
}
.descriptionEdit {
  max-width: 400px;
  width: 100%;
  margin-left: 40px;
  white-space: nowrap;
  box-sizing: border-box;
  color: #464646;
  & button {
    display: block;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: 0px;
    padding: 4px 12px;
    cursor: pointer;
  }
  & b {
    padding-left: 4px;
  }
  & > div:not(:nth-child(1)) {
    padding-top: 12px;
  }
  & > div:nth-child(1) {
    text-transform: uppercase;
    color: #464646;
  }
  & > div:nth-child(2) > div, & > div:nth-child(3) > div {
    width: 100%;
    padding-left: 8px;
  }
  & > div:nth-child(1), & > div:nth-child(2), & > div:nth-child(3) {
    display: flex;
    align-items: center;
  }
  & > div:nth-child(6) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  & > div:nth-child(6) > div {
    padding-top: 8px;
    padding-left: 4px;
    text-transform: capitalize;
    & > div {
      margin-left: -4px;
    }
  }
  & input:not([type='checkbox']), & textarea {
    padding: 4px 8px;
    width: 100%;
    border: none;
    box-sizing: border-box;
  }
}

.status>span{
  color: #969696;
  font-size: 18px;
  cursor: pointer;
  &:hover {
    opacity:.6;
  }
}

.description__info {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding-top: 8px;
  & img {
    cursor: pointer;
    &:hover{
      opacity: .6;
    }
  }
}

.description__infoContainer {
  padding-left: 8px;
  color: #464646;
}

.description__fullName {
  font-size: 32px; 
}

.description__lookingForAJob {
  padding: 4px 0px;
}
.description__contacts {
  padding-top: 12px;
}

.description__contactsContainer {
  display: flex;
  flex-direction: column;
  &>div{
    padding-left: 12px;
  }
}

.description__contactTitle {
  text-transform: uppercase;
}